.parallax-page {
  height: auto;
  margin: 0 0 55px 0;
  @include breakpoint(md) {
    margin: 0 0 70px 0; }
  @include breakpoint(lg) {
    margin: 0 0 100px 0; }
  .parallax {
    position: static;
    img {
      //width: 2500px
      max-width: 2200px;
      min-width: 100%;
      min-height: 85vh;
      //min-height: 1500px
      //max-height: 85vh
      object-fit: fill;
      bottom: 0;
      @include breakpoint(md) {
        bottom: 80px; }
      @include breakpoint(lg) {
        bottom: -100px; } } }
  &__body {
    padding: 50px 0 0 0;
    display: flex;
    flex-direction: column;
    position: relative;
    z-index: 2;
    @include breakpoint(sm) {
      padding: 80px 0 0 0; }
    @include breakpoint(lg) {
      padding: 110px 15px 0 15px;
      max-width: $container-width;
      margin: 0 auto; } }

  &__nav-info {
    display: flex;
    flex-direction: column;
    background-color: rgba(0, 0, 0, 0.15);
    padding: 25px 0;
    @include breakpoint(lg) {
      display: inline-flex;
      width: fit-content;
      padding: 25px 50px;
      margin: 80px 0 0 0; } } }

.breadcumps {
  display: flex;
  align-items: center;
  margin: 0 0 12px 0; }
.breadcumps,
.breadcumps__link {
  color: #ffffff;
  font-size: 14px;
  line-height: 16px;
  @include breakpoint(md) {
    font-size: 18px;
    line-height: 21px; }
  svg {
    margin: 0 2px 0 0; }
  span {
    margin: 0 3px; }
  &__link {
    transition: color .3s ease-out;
    &:hover {
      color: #7EB5F9; } } }
